import React, { useEffect, useState } from 'react';
import './AdminPanel.css';
import User from '../../../../models/user';
import dbService from '../../../../services/dbService';

const AdminPanel = () => {
    const [users, setUsers] = useState<User[]>([]);

    useEffect(() => {
        dbService.getAllUsers().then(resp => setUsers(resp));
    }, []);

    // Effects
    return (
        <div className="AdminPanel">
            <h2>Panell d'administrador</h2>
            <div className="adminControls">
                <div className="button" onClick={() => dbService.assignTargets()}>
                    Assignar objectius
                </div>
            </div>
            <table id="users">
                <thead>
                    <tr className="header">
                        <td>Id</td>
                        <td>Email</td>
                        <td>Nom</td>
                        <td>Instagram</td>
                        <td>Bar</td>
                        <td>Any</td>
                        <td>Amic1</td>
                        <td>Amic2</td>
                        <td>Institut</td>
                        <td>Estat</td>
                        <td>Img</td>
                        <td>TargetId</td>
                        <td>Verificat</td>
                        <td>IP</td>
                        <td>Killed</td>
                        <td>approved</td>
                    </tr>
                </thead>
                <tbody>
                    {users.map((user, i) => {
                        var birthDate: Date = new Date(user.birthDate);

                        return (
                            <tr key={i} className="">
                                <td>{user.itemId}</td>
                                <td>{user.email}</td>
                                <td>{user.userName}</td>
                                <td><a href={`https://www.instagram.com/${user.instagram}`} target="_blank">@{user.instagram}</a></td>
                                <td>{user.bar}</td>
                                <td>{birthDate.getFullYear()}</td>
                                <td>{user.friendA}</td>
                                <td>{user.friendB}</td>
                                <td>{user.school}</td>
                                <td>{user.alive ? 'Viu' : 'Mort'}</td>
                                <td>
                                    <a href={`https://firebasestorage.googleapis.com/v0/b/pastanaga-marrinxa.appspot.com/o/${user.itemId}.${user.imageType}?alt=media`} target="_blank">img</a>
                                </td>
                                <td>{user.targetId}</td>
                                <td>{user.verified ? "Si" : ""}</td>
                                <td>{user.ip}</td>
                                <td>{user.killed}</td>
                                <td className={`buttonCell ${user.approved ? "yes" : "no"}`} onClick={() => dbService.approveUser(user)}>
                                    {user.approved ? "Si" : "No"}
                                </td>
                            </tr>
                        )
                    })}
                </tbody>
            </table>
        </div>
    );
}

export default AdminPanel;
