import React, { useState, useEffect, useRef } from 'react';
import './Home.css';

import authService from "../../services/authService";
import GameStats from '../../components/GameStats/GameStats';
import Politica from '../Politica/Politica';
import Contact from '../../components/Contact/Contact';
import CookieBanner from 'react-cookie-banner';
import WarningIcon from '../../components/Icons/WarningIcon';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link,
    Redirect,
    useHistory,
    useLocation
} from "react-router-dom";

const initialSignInState = {
    email: '',
    password: '',
    password_repeat: '',
    username: '',
    instagram: '',
    bar: '',
    birthDate: '',
    friendA: '',
    friendB: '',
    school: ''
}

const initialLogInState = {
    email: '',
    password: ''
}

const Home = () => {

    let history = useHistory();
    const [displayModal, setDisplayModal] = useState<boolean>(false);
    const [formToggle, setFormToggle] = useState<boolean>(false); // True = register / false = loggin

    const SignInForm = () => {
        const [signInForm, setSignInForm] = useState(initialSignInState)
        const [isMismatchError, setMismatchError] = useState(false)
        const [isExistsError, setExistsError] = useState(false)
        const [isImageError, setImageError] = useState(false)
        const fileInput = useRef<HTMLInputElement>(null);

        const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
            e.preventDefault()

            if (signInForm.password === signInForm.password_repeat) {
                if (fileInput != null && fileInput.current != null && fileInput.current.files != null) {

                    let file = fileInput.current.files[0];
                    //dbService.uploadImage(file);

                    authService.register(
                        signInForm.email,
                        signInForm.password,
                        signInForm.username,
                        signInForm.instagram,
                        signInForm.bar,
                        signInForm.birthDate,
                        file,
                        signInForm.friendA,
                        signInForm.friendB,
                        signInForm.school
                    ).then(resp => {
                        if (resp == "OK") {
                            setSignInForm({
                                email: '',
                                password: '',
                                password_repeat: '',
                                username: '',
                                instagram: '',
                                bar: '',
                                birthDate: '',
                                friendA: '',
                                friendB: '',
                                school: ''
                            });
                            setFormToggle(false);
                        } else if (resp == "auth/email-already-in-use") {
                            setExistsError(true);
                        }
                    });
                } else {
                    setImageError(true);
                }
            } else {
                setMismatchError(true);
            }
        }

        return (
            <div className="HomeForm">
                {false && <p>Les inscripcions han tancat.</p>}
                {true && <form onSubmit={handleSubmit}>
                    <div className="formRow">
                        <div className="inputContainer">
                            <label>Nom complet</label>
                            <input type="text"
                                required
                                placeholder="David Bote Paz"
                                value={signInForm.username}
                                onChange={val => setSignInForm({ ...signInForm, username: val.target.value })} />
                        </div>
                        <div className="inputContainer">
                            <label>Correu electrònic</label>
                            <input type="email"
                                required
                                placeholder="Correu electrònic"
                                value={signInForm.email}
                                onChange={val => {
                                    setSignInForm({ ...signInForm, email: val.target.value })
                                    setExistsError(false);
                                }} />
                        </div>
                    </div>
                    <div className="formRow">
                        <div className="inputContainer">
                            <label>Contrasenya</label>
                            <input type="password"
                                required
                                placeholder="Contrasenya"
                                value={signInForm.password}
                                onChange={val => {
                                    setSignInForm({ ...signInForm, password: val.target.value })
                                    setMismatchError(false)
                                }} />
                        </div>
                        <div className="inputContainer">
                            <label>Repeteix la contrasenya</label>
                            <input type="password"
                                required
                                placeholder="Repeteix la contrasenya"
                                value={signInForm.password_repeat}
                                onChange={val => {
                                    setSignInForm({ ...signInForm, password_repeat: val.target.value })
                                    setMismatchError(false)
                                }} />
                        </div>
                    </div>
                    <div className="sectionDiv" />
                    <div className="formTitle">Informació pública</div>
                    <div className="formInfo">Els altres usuaris podran veure aquesta informació (La pastanaga reina comprovarà que la informació sigui verídica per a que puguis jugar).</div>
                    <div className="formRow">
                        <div className="inputContainer">
                            <label>Usuari d'instagram</label>
                            <input type="text"
                                required
                                placeholder="Usuari d'Instagram"
                                minLength={3}
                                value={signInForm.instagram}
                                onChange={val => {
                                    let ig = val.target.value.replace("@", "");
                                    setSignInForm({ ...signInForm, instagram: ig });
                                }}
                            />
                        </div>
                        <div className="inputContainer">
                            <label>El teu bar</label>
                            <input type="text"
                                required
                                placeholder="Quin bar freqüentes?"
                                minLength={3}
                                value={signInForm.bar}
                                onChange={val => setSignInForm({ ...signInForm, bar: val.target.value })} />
                        </div>
                    </div>
                    <div className="formRow">
                        <div className="inputContainer">
                            <label>Data de naixement</label>
                            <input type="date"
                                required
                                placeholder="Any de naixement"
                                value={signInForm.birthDate}
                                onChange={val => setSignInForm({ ...signInForm, birthDate: val.target.value })}
                                min="1900-01-01" max="2003-07-01" />
                        </div>
                        <div className="inputContainer">
                            <label>Imatge de perfil <span className="smallLabel">(On se't identifiqui!)</span></label>
                            <input required type="file" ref={fileInput} accept="image/png, image/jpeg" />
                        </div>
                    </div>
                    <div className="sectionDiv" />
                    <div className="formTitle">Informació secreta</div>
                    <div className="formInfo">Aquesta informació només la podran veure aquells usuaris que superin algunes proves.</div>
                    <div className="formRow">
                        <div className="inputContainer">
                            <label>El nom d'un amic teu</label>
                            <input type="text"
                                required
                                placeholder="Pedro Sánchez Pérez-Castejón"
                                minLength={3}
                                value={signInForm.friendA}
                                onChange={val => setSignInForm({ ...signInForm, friendA: val.target.value })} />
                        </div>
                        <div className="inputContainer">
                            <label>El nom d'un altre amic</label>
                            <input type="text"
                                required
                                placeholder="Miquel Iceta i Llorens"
                                minLength={3}
                                value={signInForm.friendB}
                                onChange={val => setSignInForm({ ...signInForm, friendB: val.target.value })} />
                        </div>
                    </div>
                    <div className="formRow">
                        <div className="inputContainer">
                            <label>Institut</label>
                            <input type="text"
                                required
                                placeholder="A quin institut vas estudiar?"
                                minLength={3}
                                value={signInForm.school}
                                onChange={val => setSignInForm({ ...signInForm, school: val.target.value })} />
                        </div>
                    </div>

                    <div className="politicRow">
                        <label>
                            <input required type="checkbox" />
                            Declaro que sóc major d'edat i que he llegit i accepto la
                            <span> <a href="https://marrinxa.org/legal/ca/avis-legal" target="_blank">política de privacitat.</a></span>
                        </label>
                    </div>
                    {isMismatchError && <small>Les contrasenyes no coincideixen.</small>}
                    {isExistsError && <small>Ja hi ha un compte amb aquest correu.</small>}
                    {isImageError && <small>Ja hi ha un problema amb la imatge. Torna-ho a provar.</small>}
                    <input type="submit" value="Registra't" />
                </form>}
            </div>
        )
    }

    const LogInForm = () => {
        const [logInForm, setLogInForm] = useState(initialLogInState)
        const [isLogInError, setLogInError] = useState(false)

        const handleSignIn = async (e: React.FormEvent<HTMLFormElement>) => {
            e.preventDefault()

            let response = await authService.signIn(logInForm.email, logInForm.password);

            if (response) {
                history.replace("/dashboard");
            } else {
                setLogInError(true)
            }
        }

        return (
            <div className="HomeForm">
                <form onSubmit={handleSignIn} lang="ca">
                    <input type="email"
                        required
                        placeholder="Correu electrònic"
                        value={logInForm.email}
                        onChange={val => {
                            setLogInForm({ ...logInForm, email: val.target.value })
                            setLogInError(false)
                        }} />
                    <input type="password"
                        required
                        placeholder="Contrasenya"
                        value={logInForm.password}
                        onChange={val => {
                            setLogInForm({ ...logInForm, password: val.target.value })
                            setLogInError(false)
                        }} />
                    {isLogInError && <div className="formAlert">
                        <WarningIcon />
                        Revisa les teves credencials o registra't
                    </div>}
                    <input type="submit" value="Entra" />
                </form>
            </div>
        )
    }

    const clickToggle = () => {
        setFormToggle(!formToggle);
    }

    const Modal = () => {
        return <div className="Modal">
            <div className="modalContainer">
                <div className="closeModalButton" onClick={() => setDisplayModal(false)}>Tanca</div>
                <Politica />
            </div>
        </div>
    }
    return (
        <div className="Home">
            <CookieBanner
                message="Aquesta pàgina web utilitza cookies de tercers."
                onAccept={() => { }}
                cookie="user-has-accepted-cookies"
            />
            {displayModal && <Modal />}
            <div className="toggleContainter">
                <div className="toggle" >
                    <div className={`toggleButton ${formToggle ? 'notSelected' : ''}`} onClick={clickToggle}>
                        Entra
                    </div>
                    <div className={`toggleButton ${formToggle ? '' : 'notSelected'}`} onClick={clickToggle}>
                        Registra't
                    </div>
                </div>
                {formToggle && <SignInForm />}
                {!formToggle && <LogInForm />}
            </div>
            <Contact />
        </div >
    );
}

export default Home;
