import React, { useState, useEffect } from 'react';
import './GameStats.css';
import AliveImage from "../../assets/alive.png";
import DeadImage from "../../assets/dead.png";

import dbService from "../../services/dbService";

const GameStats = () => {
    const [aliveUsers, setAliveUsers] = useState(0);
    const [deadUsers, setDeadUsers] = useState(0);

    useEffect(() => {
        dbService.getAliveUsersCount(setAliveUsers);
        dbService.getDeadUsersCount(setDeadUsers);
    }, []);

    return (
        <div className="gameStats">
            <div className="marcador">
                <img src={AliveImage} />
                <div className="label">{aliveUsers} pastanagues jugant</div>
            </div>
            <div className="marcador">
                <img src={DeadImage} />
                {deadUsers != 1 && <div className="label">{deadUsers} pastanagues mortes</div>}
                {deadUsers == 1 && <div className="label">1 pastanaga morta</div>}
            </div>
        </div>
    )
}

export default GameStats