import React, { useEffect, useState } from 'react';

const Instructions = () => {
    // Effects
    return (
        <div>
            <p>La Pastanaga Assassina que us proposem és un joc de rol. Cada participant té un doble paper: de víctima i d'assassí. Abans de començar es repartirà a cada participant el nom d'una persona aleatòriament a través d'aquesta aplicació web. La persona que se us assigni serà la víctima que haureu de matar, tocant-la amb una pastanaga. Un cop "mort", queda eliminada del joc, per fer-ho oficial haurà de facilitar-te el codi que se li haurà adjudicat des del seu perfil i que tu hauràs d'introduir en el teu. Automàticament se t'assignarà la teva pròxima víctima. El joc acaba quan només una persona aconsegueix quedar amb vida o el dia de la Batalla Final.</p>
            <h3>FUNCIONAMENT</h3>
            <p>El joc començarà el <b>23 de juliol</b> i acabarà el dia 31. El dia que es realitza el concert oficial de l'Associació Cultural Marrinxa hi haurà un fet important. Accedint al web visualitzaràs la informació sobre el joc, les seves normes i el funcionament, un enllaç per fer consultes i preguntes i finalment, a partir del dia 23, la informació bàsica sobre la teva víctima per poder trobar-la. Si aconseguiu matar-la haureu d'introduir el seu codi i refrescar la pàgina per veure qui serà la vostra nova víctima. En canvi, si us maten, haureu de facilitar el vostre codi a la persona que us ha matat perquè aquesta pugui continuar matant.</p>
            <p>Per evitar que el joc s'encalli, cada 24 o 48 hores segurament es canvi el vostre objectiu. Per tant! Descobriu, actueu i mateu amb rapidesa i seguretat abans que us descobreixin a vosaltres. I sobretot, aneu consultant la pàgina web.</p>
            <p>Durant el joc hi haurà diverses proves per augmentar la diversió. Estaran dividides en 2 tipologies:</p>
            <ol>
                <li>Proves d'immunitat: Són aquelles que si les realitzes no et poden matar durant el dia que les hagis fet.</li>
                <li>Proves d'informació: Són aquelles que cal fer si necessites o vols informació extra sobre la teva víctima.</li>
            </ol>
            <p>Vigileu per què a la vegada que són espais per aconseguir informacions i avantatges també poden ser trampes que els enemics utilitzin per anar a buscar les seves preses! En els diferents espais hi haurà persones organitzadores que vetllaran i observaran que les proves es facin correctament. Les proves i els llocs corresponents s'aniran informant a través de l'Instagram de Marrinxa.</p>
            <p>Ja per acabar, us informem que hi haurà 2 premis.</p>
            <ul>
                <li>Premi a la persona que guanyi el joc</li>
                <li>Premi a la persona que mati a més persones (el recompte automàtic es farà al web)</li>
            </ul>
            <h3>NORMES DE L’ASSASSÍ</h3>
            <p>Per tal de poder desenvolupar el joc correctament serà indispensable seguir aquestes 5 normes:</p>
            <ol>
                <li>Només es pot matar amb una pastanaga real.</li>
                <li>Per matar has d'assassinar la víctima amb la pastanaga des de davant. No es pot per l'esquena.</li>
                <li>Només pots matar qui tens assignat, no pots matar per algú altre si aquest encara està viu.</li>
                <li>No es pot matar a algú si està intercanviant codis.</li>
                <li>No passa res si altres persones (jugadors o no) veuen el crim.</li>
                <li>El joc s’ha de desenvolupar amb respecte, sense discriminar per cap raó i sense incomodar a cap persona. No es tolerarà cap tipus de violència o assetjament.</li>
            </ol>
        </div >
    );
}

export default Instructions;
