import firebase from 'firebase';
import User from '../models/user';

const publicIp = require('public-ip');
const shortid = require('shortid');

const firebaseConfig = {
    apiKey: "AIzaSyDotM8N5DwhwQRaYrT2_d4SMqZsxeIZGl4",
    authDomain: "pastanaga-marrinxa.firebaseapp.com",
    projectId: "pastanaga-marrinxa",
    storageBucket: "pastanaga-marrinxa.appspot.com",
    messagingSenderId: "559501506795",
    appId: "1:559501506795:web:9818e72ad0e84a237666ae"
};

const firebaseApp = firebase.initializeApp(firebaseConfig);
const db = firebaseApp.firestore();
const storage = firebaseApp.storage().ref();

export class DbService {

    constructor() { }

    shuffle(array: User[]) {
        var currentIndex = array.length, randomIndex;

        // While there remain elements to shuffle...
        while (0 !== currentIndex) {

            // Pick a remaining element...
            randomIndex = Math.floor(Math.random() * currentIndex);
            currentIndex--;

            // And swap it with the current element.
            [array[currentIndex], array[randomIndex]] = [
                array[randomIndex], array[currentIndex]];
        }

        return array;
    }

    async assignTargets() {
        const EXCLUDE_ADMINS: boolean = true;
        let allUsers: User[] = await this.getAllUsers();
        let usersPlaying: User[] = [];
        let usersNotPlaying: User[] = [];
        allUsers.forEach((user: User) => {
            if (user.alive && user.approved && (!user.admin || !EXCLUDE_ADMINS)) {
                usersPlaying.push(user);
            } else {
                usersNotPlaying.push(user);
            }
        })
        usersPlaying = this.shuffle(usersPlaying);
        console.log(usersPlaying);
        console.log("Assign targets:");
        for (let i = 0; i < usersPlaying.length; i++) {
            console.log(`${i} - ${usersPlaying[i].itemId} - ${usersPlaying[i].itemId + 1}`);
            this.setTarget(usersPlaying[i], usersPlaying[(i + 1) % usersPlaying.length].itemId);
        }
        console.log("Assign empty targets:");
        console.log(usersNotPlaying.length);
        for (let i = 0; i < usersNotPlaying.length; i++) {
            if (usersNotPlaying[i].targetId != "") {
                console.log(`${i} - ${usersNotPlaying[i].itemId}`);
                this.setTarget(usersNotPlaying[i], "");
            }
        }
        console.log("DONE");
    }

    async setTarget(user: User, targetId: string) {
        user.targetId = targetId;
        this.updateUser(user);
    }

    async uploadImage(file: Blob, name: string) {
        let fileType = file.type.split("/")[1];
        var imageRef = storage.child(`${name}.${fileType}`);
        imageRef.put(file);
    }

    async newUser(
        email: string,
        userName: string,
        userId: string,
        instagram: string,
        bar: string,
        birthDate: string,
        file: Blob,
        friendA: string,
        friendB: string,
        school: string
    ) {
        let shortId = shortid.generate(email);
        let imageType = file.type.split("/")[1];
        publicIp.v4().then((ip: string) => {
            db.collection("users").add({
                userName,
                email,
                alive: true,
                shortId,
                userId,
                killed: 0,
                admin: false,
                imageUrl: "",
                instagram,
                bar,
                birthDate,
                imageType,
                friendA,
                friendB,
                school,
                targetId: "",
                ip,
                approved: false,
                verified: false
            }).then((docRef) => {
                console.log("Document written with ID: ", docRef.id);
                this.uploadImage(file, docRef.id)
            })
                .catch((error) => {
                    console.error("Error adding document: ", error);
                });
        })

    }

    async getUserData(setter: any) {
        await firebase.auth().onAuthStateChanged(async (u) => {
            if (u) {
                return await db.collection("users").where("userId", "==", u.uid).get().then((querySnapshot) => {
                    console.log(querySnapshot);
                    let data = querySnapshot.docs[0].data();

                    let user: User = {
                        itemId: querySnapshot.docs[0].id,
                        alive: data.alive,
                        email: data.email,
                        killed: data.killed,
                        shortId: data.shortId,
                        userId: data.userId,
                        userName: data.userName,
                        admin: data.admin,
                        instagram: data.instagram,
                        bar: data.bar,
                        birthDate: data.birthDate,
                        imageType: data.imageType,
                        targetId: data.targetId,
                        friendA: data.friendA,
                        friendB: data.friendB,
                        school: data.school,
                        verified: data.verified,
                        ip: data.ip,
                        approved: data.approved
                    }
                    setter(user);
                });
            }
        });
    }

    async kill(killer: User, victim: User): Promise<boolean> {
        victim.alive = false;
        killer.targetId = victim.targetId;
        victim.targetId = "";
        killer.killed++;
        try {
            await this.updateUser(victim);
            await this.updateUser(killer);
            return true;
        } catch (e) {
            console.log(e);
            return false;
        }
    }

    async getUserDataById(userId: string, setter: any) {
        await db.collection("users").doc(userId).get().then((querySnapshot) => {

            let data = querySnapshot.data();
            if (data) {
                let user: User = {
                    itemId: userId,
                    alive: data.alive,
                    email: data.email,
                    killed: data.killed,
                    shortId: data.shortId,
                    userId: data.userId,
                    userName: data.userName,
                    admin: data.admin,
                    instagram: data.instagram,
                    bar: data.bar,
                    birthDate: data.birthDate,
                    imageType: data.imageType,
                    targetId: data.targetId,
                    friendA: data.friendA,
                    friendB: data.friendB,
                    school: data.school,
                    verified: data.verified,
                    ip: data.ip,
                    approved: data.approved
                }
                setter(user);
            }
        });
    }

    async getAllUsers(): Promise<User[]> {
        let users: User[] = [];
        await db.collection("users").get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
                let data = doc.data();
                let user: User = {
                    itemId: doc.id,
                    alive: data.alive,
                    email: data.email,
                    killed: data.killed,
                    shortId: data.shortId,
                    userId: data.userId,
                    userName: data.userName,
                    admin: data.admin,
                    instagram: data.instagram,
                    bar: data.bar,
                    birthDate: data.birthDate,
                    imageType: data.imageType,
                    targetId: data.targetId,
                    friendA: data.friendA,
                    friendB: data.friendB,
                    school: data.school,
                    verified: data.verified,
                    ip: data.ip,
                    approved: data.approved
                }
                users.push(user);
            });
        });
        console.log(users);
        return users;
    }

    async approveUser(user: User) {
        user.approved = !user.approved;
        console.log(user);
        this.updateUser(user);
    }

    async getAliveUsersCount(setter: any) {
        await db.collection("users").where("alive", "==", true).get().then((querySnapshot) => {
            setter(querySnapshot.docs.length);
        });
    }

    async getDeadUsersCount(setter: any) {
        await db.collection("users").where("alive", "==", false).get().then((querySnapshot) => {
            setter(querySnapshot.docs.length);
        });
    }

    async updateUser(user: User) {
        db.collection("users").doc(user.itemId).update(user);
    }
}
export default new DbService();