import React, { useEffect, useState } from 'react';
import './Dashboard.css';
import User from '../../models/user';
import dbService from '../../services/dbService';
import authService from '../../services/authService';
import GameStats from '../../components/GameStats/GameStats';
import AdminPanel from './components/AdminPanel/AdminPanel';
import InstagramIcon from '../../components/Icons/InstagramIcon';
import BarIcon from '../../components/Icons/BarIcon';
import CalendarIcon from '../../components/Icons/CalendarIcon';
import Loader from '../../components/Loader/Loader';
import Contact from '../../components/Contact/Contact';
import ProfileIcon from '../../components/Icons/ProfileIcon';
import TargetIcon from '../../components/Icons/TargetIcon';
import HappyIcon from '../../components/Icons/HappyIcon';
import SadIcon from '../../components/Icons/SadIcon';
import PlayIcon from '../../components/Icons/PlayIcon';
import WarningIcon from '../../components/Icons/WarningIcon';
import OkayIcon from '../../components/Icons/OkayIcon';
import AlertIcon from '../../components/Icons/AlertIcon';
import ArrowDownIcon from '../../components/Icons/ArrowDownIcon';
import ArrowRightIcon from '../../components/Icons/ArrowRightIcon';
import Instructions from './components/Instructions';

const Dashboard = () => {
    const START_DATE: Date = new Date("Jul 22, 2023 00:00:00");
    var today = new Date();
    const GAME_STARTED: boolean = today > START_DATE;
    const [showingInstructions, setShowingInstructions] = useState<boolean>(false);
    const [displayOkayMessage, setDisplayOkayMessage] = useState<boolean>(false);
    const [userVerified, setUserVerified] = useState<boolean>(false);
    const [verificationMailSent, setVerificationMailSent] = useState<boolean>(false);
    const [countDown, setCountDown] = useState<string>('');
    const [loadingSignedUser, setLoadingSignedUser] = useState<boolean>(false);
    const [signedUser, setSignedUser] = useState<User>(new User);
    const [targetUser, setTargetUser] = useState<User>(new User);
    const [killingId, setKillingId] = useState<string>('');
    const [wrongTarget, setWrongTarget] = useState<boolean>(false);
    const [targetKilled, setTargetKilled] = useState<boolean>(false);
    const [someErrorKillig, setSomeErrorKilling] = useState<boolean>(false);

    const fetchData = () => {
        setLoadingSignedUser(true);
        dbService.getUserData(signedUserSetter);

        authService.isCurrentUserVerified().then(resp => {
            setUserVerified(resp);
        });
    }

    useEffect(() => {
        fetchData();
        // Set countdown
        setInterval(() => {
            updateCountDown()
        }, 1000);
    }, []);

    useEffect(() => {
        if (signedUser.targetId != "") {
            dbService.getUserDataById(signedUser.targetId, setTargetUser);
        }
    }, [signedUser]);

    useEffect(() => {
        // Set user as verified in DB
        if (userVerified && signedUser && signedUser.itemId && signedUser.itemId != "" && !signedUser.verified) {
            let _user: User = signedUser;
            _user.verified = true;
            dbService.updateUser(_user);
        }
    }, [userVerified, signedUser]);

    const signedUserSetter = (user: User) => {
        setSignedUser(user);
        setLoadingSignedUser(false);
    }

    const updateCountDown = () => {
        var now = new Date().getTime();
        var distance = START_DATE.getTime() - now;
        var days = Math.floor(distance / (1000 * 60 * 60 * 24));
        var hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
        var minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
        var seconds = Math.floor((distance % (1000 * 60)) / 1000);
        var str: string = `${days} dies, ${hours} hores, ${minutes} minuts i ${seconds} segons`;
        setCountDown(str);
    }

    const tryKill = () => {
        if (killingId == targetUser.shortId) {
            dbService.kill(signedUser, targetUser).then(resp => {
                if (resp) {
                    fetchData();
                    setTargetKilled(true);
                    setKillingId('');
                    setTimeout(() => setTargetKilled(false), 2000);
                } else {
                    setSomeErrorKilling(true);
                }
            });

        } else {
            setWrongTarget(true);
            setTimeout(() => setWrongTarget(false), 2000);
        }
    }

    const sendVerificationEmail = () => {
        authService.verifyCurrentUser();
        setLoadingSignedUser(false);
    }

    // Effects
    return (
        <div className="Dashboard">
            <GameStats />
            <div className="dashboardRow">
                <div className="infoSection alert big">
                    <div className="title pointer" onClick={() => setShowingInstructions(!showingInstructions)}>
                        <AlertIcon />
                        Instruccions
                        <div className="sectionToggleContainer">
                            {showingInstructions ? <ArrowDownIcon /> : <ArrowRightIcon />}
                        </div>
                    </div>
                    {showingInstructions && <div className="infoContainer">
                        <Instructions />
                    </div>}
                </div>
            </div>
            <div className="dashboardRow">
                <div className="infoSection">
                    <div className="title">
                        <ProfileIcon />
                        El teu perfil
                        <div className="killingCount">
                            <div>
                                Has matat a <strong>{signedUser.killed}</strong> pastanagues
                            </div>
                        </div>
                    </div>
                    {loadingSignedUser && <div><Loader /></div>}
                    {!loadingSignedUser && <div className="infoContainer">
                        <div className="row">
                            <div className="imageContainer">
                                <a href={`https://firebasestorage.googleapis.com/v0/b/pastanaga-marrinxa.appspot.com/o/${signedUser.itemId}.${signedUser.imageType}?alt=media`} target="_blank">
                                    <img src={`https://firebasestorage.googleapis.com/v0/b/pastanaga-marrinxa.appspot.com/o/${signedUser.itemId}.${signedUser.imageType}?alt=media`} />
                                </a>
                            </div>
                            <div className="info">
                                <div><b>{signedUser.userName}</b></div>
                                <div className="iconLabel">
                                    <InstagramIcon />
                                    <a href={`https://www.instagram.com/${signedUser.instagram}`} target="_blank">@{signedUser.instagram}</a>
                                </div>
                                <div className="iconLabel"><BarIcon />{signedUser.bar}</div>
                                <div className="iconLabel"><CalendarIcon />{(new Date(signedUser.birthDate)).getFullYear()}</div>
                            </div>
                        </div>
                    </div>}
                </div>
                {!loadingSignedUser && (!userVerified && !signedUser.verified) && <div className="infoSection alert">
                    <div className="title">
                        Verifica el teu compte
                    </div>
                    <div className="infoContainer vertical">
                        <div>Per a poder jugar, verifica el teu correu.</div>
                        <div>Si ja has verificat el teu correu, surt i torna a entrar.</div>
                        {!verificationMailSent && <p> No has rebut el correu de verificació? Podria estar a SPAM.</p>}
                        {!verificationMailSent && <div className="verificationButton" onClick={sendVerificationEmail}> Reenvia el correu de verificació </div>}
                        {verificationMailSent && <div className="mailSentMessage">S'ha enviat el correu de verificació. Accedeix a l'enllaç i torna a carregar aquesta pàgina.</div>}
                    </div>
                </div>}
                {(userVerified || signedUser.verified) && !loadingSignedUser && !signedUser.approved && <div className="infoSection alert">
                    <div className="title">
                        <AlertIcon />
                        Pendent d'aprovació
                    </div>
                    <div className="infoContainer vertical">
                        <div>El teu compte encara no s'ha aprovat.</div>
                        <p>La <strong>Pastanaga Reina</strong> està revisant les teves dades i aprovarà el teu compte aviat.
                        </p>
                    </div>
                </div>}
                {(userVerified || signedUser.verified) && signedUser.approved && !GAME_STARTED && <div className="infoSection">
                    <div className="title">
                        Tot a punt!
                    </div>
                    <div className="infoContainer vertical">
                        <p>El joc començarà en</p>
                        <div>{countDown}</div>
                    </div>
                </div>}
                {(userVerified || signedUser.verified) && signedUser.approved && GAME_STARTED && <div className="infoSection">
                    <div className="title">
                        <TargetIcon />
                        El teu objectiu
                    </div>
                    {targetUser.itemId && <div className="infoContainer">
                        <div className="row">
                            <div className="imageContainer">
                                <a href={`https://firebasestorage.googleapis.com/v0/b/pastanaga-marrinxa.appspot.com/o/${targetUser.itemId}.${targetUser.imageType}?alt=media`} target="_blank">
                                    <img src={`https://firebasestorage.googleapis.com/v0/b/pastanaga-marrinxa.appspot.com/o/${targetUser.itemId}.${targetUser.imageType}?alt=media`} />
                                </a>
                            </div>
                            <div className="info">
                                <div><b>{targetUser.userName}</b></div>
                                <div className="iconLabel">
                                    <InstagramIcon />
                                    <a href={`https://www.instagram.com/${targetUser.instagram}`} target="_blank">@{targetUser.instagram}</a>
                                </div>
                                <div className="iconLabel"><BarIcon />{targetUser.bar}</div>
                                <div className="iconLabel"><CalendarIcon />{(new Date(targetUser.birthDate)).getFullYear()}</div>
                            </div>
                        </div>
                    </div>}
                    {!targetUser.itemId && <div className="infoContainer vertical">
                        <p>No tens cap objectiu assignat. </p>
                        <p>Si creus que és un error, posa't en contacte amb <strong><a href="https://www.instagram.com/marrinxa_mataro/" target="_blank">Marrinxa</a></strong>.</p>
                    </div>}
                </div>}
            </div>
            {
                (userVerified || signedUser.verified) && signedUser.approved && GAME_STARTED && <div className="dashboardRow">
                    <div className="infoSection">
                        <div className="title">
                            <SadIcon />
                            T'han matat?
                        </div>
                        <div className="infoContainer vertical">
                            <p>Diga-li al teu assassí el teu codi:</p>
                            <b>{signedUser.shortId}</b>
                        </div>
                    </div>
                    <div className="infoSection">
                        <div className="title">
                            <HappyIcon />
                            Has matat al teu objectiu?
                        </div>
                        <div className="infoContainer vertical">
                            <p>Insereix el codi de la pastanaga <strong>{targetUser.userName}</strong>:</p>
                            <input
                                type="text"
                                placeholder="0a1b2C-3"
                                className="shortIdInput"
                                value={killingId}
                                onChange={(e) => {
                                    setKillingId(e.target.value);
                                    setWrongTarget(false);
                                    setTargetKilled(false);
                                }}
                            />
                            <div className="killButton" onClick={tryKill}>
                                <PlayIcon />
                                Mata
                            </div>
                            {wrongTarget && <div className="errorMessage">
                                <WarningIcon />
                                Aquest no és el codi de <strong>{targetUser.userName}</strong>
                            </div>}
                            {targetKilled && <div className="okMessage">
                                <OkayIcon />
                                Has matat al teu objectiu. Tens una nova missió.
                            </div>}
                            {someErrorKillig && <div className="errorMessage">Hi ha hagut algun error. Torna-ho a provar</div>}
                        </div>
                    </div>
                </div>
            }
            <div className="dashboardRow">
                <Contact />
            </div>
            <div className="dashboardRow">
                {signedUser.admin &&
                    <AdminPanel />
                }
            </div>
        </div >
    );
}

export default Dashboard;
