export default class User {
    constructor() {
        this.itemId = "";
        this.alive = true;
        this.email = "";
        this.killed = 0;
        this.shortId = "";
        this.userId = "";
        this.userName = "";
        this.admin = false;
        this.instagram = "";
        this.birthDate = "";
        this.bar = "";
        this.imageType = "";
        this.targetId = "";
        this.friendA = "";
        this.friendB = "";
        this.school = "";
        this.verified = false;
        this.ip = "";
        this.approved = false;
    }
    // Id item
    public itemId!: string;

    // Informació identificativa usuari
    public userId!: string;
    public shortId!: string;
    public email!: string;

    // Informació del joc
    public alive!: boolean;
    public killed!: number;

    // Informació pública
    public userName!: string;
    public instagram!: string;
    public bar!: string;
    public birthDate!: string;

    // Informació secreta
    public friendA!: string;
    public friendB!: string;
    public school!: string;

    // Útils
    public imageType!: string;
    public admin!: boolean;
    public verified!: boolean;
    public approved!: boolean;
    public ip!: string;

    //Objectiu
    public targetId!: string;
}