import firebase from 'firebase';
import dbService from './dbService';
import User from '../models/user';

export class AuthService {

    constructor() { }

    async verifyUser(user: any) {
        if (user) {
            user.sendEmailVerification().then(function (e: any) {
                console.log(e);
            }).catch(function (error: any) {
                console.log(error);
            });
        }
    }

    async verifyCurrentUser() {
        var user = firebase.auth().currentUser;
        this.verifyUser(user);
    }

    async isCurrentUserVerified(): Promise<boolean> {
        var user = firebase.auth().currentUser;
        if (user) {
            return user.emailVerified
        }
        return false;
    }

    async register(mail: string, password: string, userName: string, instagram: string, bar: string, birthDate: string, file: Blob, friendA: string, friendB: string, school: string): Promise<string> {
        let result = "";

        await firebase.auth().createUserWithEmailAndPassword(mail, password)
            .then((userCredential) => {
                var user = userCredential.user;
                var userId = user?.uid;
                if (!userId) { userId = ""; }
                dbService.newUser(mail, userName, userId, instagram, bar, birthDate, file, friendA, friendB, school);
                if (user) {
                    this.verifyUser(user);
                }
                result = "OK"
            })
            .catch((error) => {
                var errorCode = error.code;
                var errorMessage = error.message;
                result = errorCode;
            });
        return result;
    }

    async signIn(mail: string, password: string) {
        return await firebase.auth().signInWithEmailAndPassword(mail, password)
            .then((userCredential) => {
                var user = userCredential.user;
                return true;
            })
            .catch((error) => {
                var errorCode = error.code;
                var errorMessage = error.message;
                return false;
            });
    }
}
export default new AuthService();